<template>
  <div class="twelve wide column page-body">
    <div class="header">
      <h2 class="ui header">
        <label v-bind:class="[ lableColor(this.viewBlogCat) ]" class="ui label">
          {{ this.viewBlogCat }}
        </label>
        {{ this.viewBlogTitle }}
      </h2>
      <div class="meta">
        <span class="date">{{ this.viewBlogDate }} by {{ this.viewBlogAuthor }}</span>
      </div>
      <div class="ui section divider"></div>
    </div>
    <div class="content" v-html="this.viewBlogHtml">
    </div>
    <div class="ui section divider custom-divider"></div>
    <div id="disqus_thread"></div>
  </div>
</template>

<script>
import getJSON from 'simple-get-json/index-es2017';
import main from 'md2json2html';

export default {
  name: 'Blog',
  data() {
    return {
      viewBlogTitle: '',
      viewBlogDate: '',
      viewBlogAuthor: '',
      viewBlogCat: '',
      viewBlogHtml: '',
    };
  },
  methods: {
    lableColor(cat) {
      return this.$common.findLabel(this.$const, cat);
    },
  },
  async beforeCreate() {
    const blog = await getJSON(`/static/${this.$route.path}.json`);

    this.viewBlogTitle = blog.meta.title;
    this.viewBlogDate = blog.meta.date;
    this.viewBlogAuthor = blog.meta.author;
    this.viewBlogCat = blog.meta.category;
    this.viewBlogHtml = main.json2html(blog.content);
  },
  created() {
    this.$disqus.initConfig(window, this.$route.fullPath, this.$route.path);
    this.$disqus.initDisqus(document);
  },
};
</script>

<style scoped>
.header {
  padding: 15px 0 0 0;
}

span.date {
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
  color: #8c8c8c;
}

h2 {
  margin-bottom: 0 !important;
}

.divider {
  margin-top: 0 !important;
}

.custom-divider {
  margin: 50px 0 70px 0 !important;
}
</style>



